<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querySearch||'搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div class="sort-item" @click="getSearch('all')">综合</div>
      <div class="sort-item" @click="getSearch('sales')">销量</div>
      <div class="sort-item" @click="getSearch('price',xl=xl===0? 1 : 0)">价格 </div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getSearchData } from '@/api/product'
export default {
  name: 'SearchIndex',
  components: {
    GoodsItem
  },
  computed: {
    // 获取地址的搜索关键字
    querySearch () {
      return this.$route.query.search
    }
  },
  data () {
    return {
      // 页码
      page: 1,
      proList: [],
      xl: 0
    }
  },
  async created () {
    this.getSearch()
  },
  methods: {
    // 综合排序
    async getSearch (sortType, sortPrice) {
      const { data: { list } } = await getSearchData({
        categoryId: this.$route.query.categoryId,
        sortType: sortType,
        sortPrice: sortPrice,
        goodsName: this.querySearch,
        page: this.page
      })
      this.proList = list.data
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
